.about-container {
    padding: 20px;
    max-width: 800px;
    margin: auto;
    font-family: 'Arial', sans-serif;
  }
  
  .about-container h1 {
    color: #333;
  }
  
  .about-container p {
    color: #666;
    line-height: 1.6;
  }
  
  .contact-info {
    margin-top: 20px;
  }
  
  .contact-info a {
    color: #0073e6;
    text-decoration: none;
  }
  
  .contact-info a:hover {
    text-decoration: underline;
  }